import React, { Suspense } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Snackbar, SnackbarContent, CircularProgress } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { renderRoutes } from "react-router-config";
// TODO: Update this import when Material UI moves this to production.
import MuiAlert from "@material-ui/lab/Alert";

import SideNavigation from "./SideNavigation";
import routes from "../../routes";
import { closeNotification } from "../../redux/actions";

const Alert = (props) => <MuiAlert elevation={6} variant="filled" {...props} />;

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex",
        flexDirection: "row",
        width: "100%",
        // backgroundColor: "#EEEEEE",
    },
    content: {
        // marginTop: 16,
        // backgroundColor: "EEEEEE",
        width: "calc(100% - 256px)",
    },
    progress: {
        maxWidth: 24,
        maxHeight: 24,
        color: "white",
    },
    drawer: {
        width: 50,
    },
    suspense: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        minHeight: "100vh",
        backgroundColor: theme.palette.background.default,
        width: "100%",
    },
}));

// TODO: The layouts should be configurable.
// TODO: Show drawer instead of toolbar for smaller screens.
const MainLayout = (props) => {
    const { children, settings } = props;
    const classes = useStyles();
    const dispatch = useDispatch();
    const notification = useSelector((state) => state.notification);

    const handleCloseNotification = (event, reason) => {
        if (reason !== "clickaway") {
            dispatch(closeNotification());
        }
    };

    const renderNotification = (notification0) => {
        if (notification0) {
            if (notification0.category === "LOADING") {
                return (
                    <Snackbar
                        open={true}
                        autoHideDuration={6000}
                        onClose={handleCloseNotification}>
                        <SnackbarContent
                            message={notification0.message}
                            action={
                                <CircularProgress
                                    className={classes.progress}
                                />
                            }
                        />
                    </Snackbar>
                );
            }

            return (
                <Snackbar
                    open={true}
                    autoHideDuration={6000}
                    onClose={handleCloseNotification}>
                    <Alert
                        severity={notification0.category}
                        onClose={handleCloseNotification}>
                        {notification0.message}
                    </Alert>
                </Snackbar>
            );
        }
        return null;
    };

    const renderFallback = () => (
        <div className={classes.suspense}>
            <CircularProgress size="48px" />
        </div>
    );

    return (
        <React.Fragment>
            <div className={classes.root}>
                <SideNavigation />
                <main className={classes.content}>
                    <Suspense fallback={renderFallback()}>
                        {renderRoutes(routes)}
                    </Suspense>
                    {children}
                </main>
            </div>

            {renderNotification(notification)}
        </React.Fragment>
    );
};

export default MainLayout;
