import React from "react";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import {
    ButtonBase,
    Typography,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
    Divider,
    Icon,
    IconButton,
    Menu,
    MenuItem,
    Drawer,
} from "@material-ui/core";
import { useLocation, useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useGoogleLogout } from "react-google-login";

import { logout } from "../../redux/actions";
import { trackSideNavigationClick } from "../../util/analytics";

const drawerWidth = 256;

const useStyles = makeStyles(() => ({
    drawer: {
        width: drawerWidth,
    },
    drawerPaper: {
        width: drawerWidth,
    },
    root: {
        width: "100%",
        height: "100vh",
        borderRadius: 16,
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        margin: 0,
    },
    groups: {},
    group: {},
    listItemIcon: {
        minWidth: 40,
    },
    listItemText: {
        fontSize: 14,
    },
    user: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        marginBottom: 16,
        marginRight: 8,
    },
    userDetails: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "flex-start",
        width: 192,
        paddingLeft: 16,
        paddingRight: 16,
    },
    picture: {
        width: 32,
        height: 32,
        borderRadius: 16,
    },
    userName: {
        fontSize: 14,
        fontWeight: 600,
        marginLeft: 16,
    },
    userMenu: {
        width: 184,
    },
    userMenuItem: {
        fontSize: 14,
    },
}));

const groups = [
    {
        id: "general",
        children: [
            {
                id: "home",
                title: "Home",
                icon: "home",
                url: "/home",
            },
        ],
    },
    {
        id: "articles",
        children: [
            {
                id: "create-article",
                title: "Create Article",
                icon: "add_circle",
                url: "/articles/new",
            },
            {
                id: "articles",
                title: "Articles",
                icon: "article",
                url: "/articles",
            },
        ],
    },
    {
        id: "courses",
        children: [
            {
                id: "create-course",
                title: "Create Course",
                icon: "add_circle",
                url: "/courses/new",
            },
            {
                id: "courses",
                title: "Courses",
                icon: "book",
                url: "/courses",
            },
        ],
    },
    {
        id: "support",
        children: [
            {
                id: "feedback",
                title: "Feedback",
                icon: "feedback",
                url: "/feedback",
            },
        ],
    },
];

const SideNavigation = () => {
    const classes = useStyles();
    const location = useLocation();
    const history = useHistory();
    const theme = useTheme();
    const { firstName, lastName, pictureURL } =
        useSelector((state) => state.session.payload) || {};
    const [userMenuAnchor, setUserMenuAnchor] = React.useState(null);
    const { signOut } = useGoogleLogout({
        clientId: process.env.REACT_APP_GOOGLE_OAUTH_CLIENT_ID,
    });
    const dispatch = useDispatch();

    const openUserMenuAnchor = (event) => {
        setUserMenuAnchor(event.currentTarget);
    };

    const closeUserMenuAnchor = () => {
        setUserMenuAnchor(null);
    };

    const handleClick = (id, url) => () => {
        trackSideNavigationClick(id);

        if (url.startsWith("/")) {
            history.push(url);
        } else {
            window.open(url);
        }
    };

    const handleLogout = () => {
        signOut();
        dispatch(logout());

        // setUserMenuAnchor(null);
        history.push("/index");
    };

    const renderItem = (item) => {
        const selected = location.pathname === item.url;
        return (
            <ListItem
                key={item.id}
                button={true}
                onClick={handleClick(item.id, item.url)}>
                <ListItemIcon classes={{ root: classes.listItemIcon }}>
                    <Icon
                        fontSize="small"
                        color={selected ? "secondary" : undefined}>
                        {item.icon}
                    </Icon>
                </ListItemIcon>
                <ListItemText
                    primary={item.title}
                    classes={{ primary: classes.listItemText }}
                    style={{
                        color: selected
                            ? theme.palette.secondary.main
                            : undefined,
                    }}
                />
            </ListItem>
        );
    };
    const renderGroup = (group, index) => (
        <React.Fragment key={group.id}>
            <List component="nav" className={classes.group}>
                {group.children.map(renderItem)}
            </List>
            {index + 1 < groups.length && <Divider />}
        </React.Fragment>
    );

    return (
        <Drawer
            variant="permanent"
            className={classes.drawer}
            classes={{
                paper: classes.drawerPaper,
            }}>
            <div className={classes.root}>
                <div className={classes.groups}>{groups.map(renderGroup)}</div>
                <div className={classes.user}>
                    <ButtonBase
                        className={classes.userDetails}
                        onClick={handleClick("/profile")}>
                        {/* eslint-disable-next-line jsx-a11y/alt-text */}
                        <img className={classes.picture} src={pictureURL} />
                        <Typography className={classes.userName}>
                            {firstName} {lastName}
                        </Typography>
                    </ButtonBase>
                    <IconButton onClick={openUserMenuAnchor}>
                        <Icon>settings</Icon>
                    </IconButton>
                    <Menu
                        anchorEl={userMenuAnchor}
                        keepMounted={true}
                        open={Boolean(userMenuAnchor)}
                        onClose={closeUserMenuAnchor}
                        classes={{ paper: classes.userMenu }}>
                        <MenuItem
                            className={classes.userMenuItem}
                            onClick={closeUserMenuAnchor}>
                            Preferences
                        </MenuItem>
                        <MenuItem
                            className={classes.userMenuItem}
                            onClick={handleLogout}>
                            Logout
                        </MenuItem>
                    </Menu>
                </div>
            </div>
        </Drawer>
    );
};

export default SideNavigation;
