import {
    LOGOUT,
    FETCH_SESSION_SUCCESS,
    FETCH_SESSION_FAIL,
    FETCH_SESSION,
    CREATE_SESSION_SUCCESS,
    CREATE_SESSION_FAIL,
    CREATE_SESSION,
} from "../actionTypes";

const INITIAL_STATE = {
    payload: null,
    loading: false,
    status: null,
};

const sessionReducer = (state = INITIAL_STATE, action) => {
    const { type, payload } = action;
    switch (type) {
        case LOGOUT: {
            return {
                ...state,
                payload: null,
                loading: false,
                status: LOGOUT,
            };
        }

        case FETCH_SESSION_SUCCESS: {
            return {
                ...state,
                payload,
                loading: false,
                status: FETCH_SESSION_SUCCESS,
            };
        }

        case FETCH_SESSION_FAIL: {
            return {
                ...state,
                payload: null,
                loading: false,
                status: FETCH_SESSION_FAIL,
            };
        }

        case FETCH_SESSION: {
            return {
                ...state,
                payload: null,
                loading: true,
                status: FETCH_SESSION,
            };
        }

        case CREATE_SESSION_SUCCESS: {
            return {
                ...state,
                payload,
                loading: false,
                status: CREATE_SESSION_SUCCESS,
            };
        }

        case CREATE_SESSION_FAIL: {
            return {
                ...state,
                payload: null,
                loading: false,
                status: CREATE_SESSION_FAIL,
            };
        }

        case CREATE_SESSION: {
            return {
                ...state,
                payload: null,
                loading: true,
                status: CREATE_SESSION,
            };
        }

        default: {
            return state;
        }
    }
};

export default sessionReducer;
