import React, { Suspense } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Snackbar, SnackbarContent, CircularProgress } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { renderRoutes } from "react-router-config";
// TODO: Update this import when Material UI moves this to production.
import MuiAlert from "@material-ui/lab/Alert";

import VisitorToolbar from "./VisitorToolbar";
// import VisitorFooter from "./VisitorFooter";
import routes from "../../routes";
import { closeNotification } from "../../redux/actions";

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.default
    },
    content: {
        transition: theme.transitions.create("margin", {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        marginTop: 64,
    },
    progress: {
        maxWidth: 24,
        maxHeight: 24,
        color: "white",
    },
    drawer: {
        width: 50,
    },
    suspense: {
        position: "fixed",
        top: "50%",
        left: "50%",
        marginTop: -24,
        marginLeft: -24,
    },
}));

// TODO: The layouts should be configurable.
// TODO: Show drawer instead of toolbar for smaller screens.
const MainLayout = (props) => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const notification = useSelector((state) => state.notification);

    const handleCloseNotification = (event, reason) => {
        if (reason !== "clickaway") {
            dispatch(closeNotification());
        }
    };

    const renderNotification = (notification0) => {
        if (notification0) {
            if (notification0.category === "LOADING") {
                return (
                    <Snackbar
                        open={true}
                        autoHideDuration={6000}
                        onClose={handleCloseNotification}>
                        <SnackbarContent
                            message={notification0.message}
                            action={
                                <CircularProgress
                                    className={classes.progress}
                                />
                            }
                        />
                    </Snackbar>
                );
            }

            const severityMap = {
                SUCCESS: "success",
                ERROR: "error",
            };
            const severity = severityMap[notification0.category]
                ? severityMap[notification0.category]
                : "SUCCESS";

            return (
                <Snackbar
                    open={true}
                    autoHideDuration={6000}
                    onClose={handleCloseNotification}>
                    <Alert
                        severity={severity}
                        onClose={handleCloseNotification}>
                        {notification0.message}
                    </Alert>
                </Snackbar>
            );
        }
        return null;
    };

    return (
        <React.Fragment>
            <div className={classes.root}>
                <VisitorToolbar />

                <main className={classes.content}>
                    <Suspense
                        fallback={
                            <CircularProgress
                                size="48px"
                                className={classes.suspense}
                            />
                        }>
                        {renderRoutes(routes)}
                    </Suspense>
                    {props.children}
                </main>

                {/* <VisitorFooter /> */}
            </div>

            {renderNotification(notification)}
        </React.Fragment>
    );
};

export default MainLayout;
