import React, { useEffect, useCallback, useMemo } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { matchRoutes } from "react-router-config";
import isEqual from "react-fast-compare";
import { useGoogleLogin } from "react-google-login";

import { FETCH_SESSION_FAIL } from "../redux/actionTypes";
import { createSession, setSettings } from "../redux/actions";

import layouts from "../layouts";
import routes from "../routes";

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.default,
        color: theme.palette.text.primary,
    },
}));

const defaultSettings = {
    layout: {
        name: "visitor",
        protected: false,
    },
};

const PrimaryLayout = (props) => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const history = useHistory();
    const location = useLocation();
    const { payload, status } = useSelector((state) => state.session);
    const previousSettings =
        useSelector((state) => state.settings) || defaultSettings;

    const matched = matchRoutes(routes, location.pathname)[0];
    const settingsAvailable = matched?.route.settings;
    const routeSettings = useMemo(
        () =>
            settingsAvailable && {
                ...defaultSettings,
                ...matched.route.settings,
            },
        [matched.route.settings, settingsAvailable],
    );

    const settings = routeSettings || previousSettings;

    useEffect(() => {
        if (settingsAvailable && !isEqual(settings, routeSettings)) {
            dispatch(setSettings(routeSettings));
        }
    }, [
        dispatch,
        location.pathname,
        routeSettings,
        settings,
        settingsAvailable,
    ]);

    useEffect(() => {
        if (settings.protected && !payload) {
            const embedURL = encodeURIComponent(
                location.pathname + location.search,
            );
            const redirectionURL = `/login?destination=${embedURL}`;
            console.log("redirectionURL", redirectionURL);
            history.push(redirectionURL);
        }
    }, [payload, status, dispatch, history, settings.protected, location]);

    const Layout = layouts[settings.layout.name];
    return <Layout className={classes.root} settings={settings} {...props} />;
};

export default PrimaryLayout;
