import {
    FETCH_SESSION_SUCCESS,
    FETCH_SESSION_FAIL,
    FETCH_SESSION,
    CREATE_SESSION_SUCCESS,
    CREATE_SESSION_FAIL,
    CREATE_SESSION,
    LOGOUT,
    FETCH_USERS_SUCCESS,
    FETCH_USERS_FAIL,
    FETCH_USERS,
} from "../actionTypes";
import * as client from "../../api";
import { trackLogout } from "../../util/analytics";
import { gqlPrivate, rest } from "../../api/network";

// Fetch Session

export const fetchSessionSuccess = (session) => ({
    type: FETCH_SESSION_SUCCESS,
    payload: session,
});

export const fetchSessionFail = () => ({
    type: FETCH_SESSION_FAIL,
    payload: null,
});

export const fetchSession = () => async (dispatch) => {
    dispatch({
        type: FETCH_SESSION,
        payload: null,
    });

    const session = JSON.parse(localStorage.getItem("session"));
    if (session) {
        rest.defaults.headers.common.Authorization = `Bearer ${session.token}`;
        gqlPrivate.defaults.headers.common.Authorization = `Bearer ${session.token}`;
        localStorage.setItem("token", session.token);
    }

    dispatch(session ? fetchSessionSuccess(session) : fetchSessionFail());
};

// Create Session

export const createSessionSuccess = (session) => ({
    type: CREATE_SESSION_SUCCESS,
    payload: session,
});

export const createSessionFail = () => ({
    type: CREATE_SESSION_FAIL,
    payload: null,
});

export const createSession = (token) => async (dispatch) => {
    dispatch({
        type: CREATE_SESSION,
        payload: null,
    });

    try {
        rest.defaults.headers.common.Authorization = `Bearer ${token}`;
        gqlPrivate.defaults.headers.common.Authorization = `Bearer ${token}`;
        localStorage.setItem("token", token);
        const session = await client.createSession(token);

        localStorage.setItem(
            "session",
            JSON.stringify(
                {
                    ...session,
                    token,
                },
                null,
                4,
            ),
        );
        window.analytics.identify(session.id, {
            name: `${session.firstName} ${session.lastName}`,
            email: session.emailAddress,
        });

        dispatch(createSessionSuccess(session));
    } catch (error) {
        dispatch(createSessionFail());
    }
};

export const logout = () => async (dispatch) => {
    trackLogout();
    window.analytics.reset();

    dispatch({
        type: LOGOUT,
        payload: null,
    });
    localStorage.removeItem("session");
};

// Fetch Users

const fetchUsersSuccess = (dispatch) => (users) => {
    dispatch({
        type: FETCH_USERS_SUCCESS,
        payload: users,
    });
};

const fetchUsersFail = (dispatch) => (error) => {
    dispatch({
        type: FETCH_USERS_FAIL,
        payload: error,
    });
};

export const fetchUsers = (params) => async (dispatch) => {
    try {
        dispatch({
            type: FETCH_USERS,
            payload: params,
        });
        const users = await client.getUsers(params);
        fetchUsersSuccess(dispatch)(users);
    } catch (error) {
        fetchUsersFail(dispatch)("Failed to fetch users");
    }
};
