import { SET_SETTINGS } from "../actionTypes";

const INITIAL_STATE = null;

const settingsReducer = (state = INITIAL_STATE, action) => {
  const { type, payload } = action;
  switch (type) {
    case SET_SETTINGS: {
      return payload;
    }

    default: {
      return state;
    }
  }
};

export default settingsReducer;
