import {
  FETCH_USERS_SUCCESS,
  FETCH_USERS_FAIL,
  FETCH_USERS,
} from "../actionTypes";

const INITIAL_STATE = {
  list: [],
  totalPages: 0,
  fetching: false,
  error: null,
  initialFetch: true,
};

const usersReducer = (state = INITIAL_STATE, action) => {
  const { type, payload } = action;
  switch (type) {
    case FETCH_USERS_SUCCESS: {
      return {
        ...state,
        list: payload.records,
        totalPages: payload.totalPages,
        fetching: false,
        initialFetch: false,
      };
    }

    case FETCH_USERS_FAIL: {
      return {
        ...state,
        fetching: false,
        error: payload,
      };
    }

    case FETCH_USERS: {
      return {
        ...state,
        fetching: true,
        error: null,
      };
    }
    default: {
      return state;
    }
  }
};

export default usersReducer;
