import {
    SHOW_NOTIFICATION,
    CLOSE_NOTIFICATION,
    SET_SETTINGS,
} from "../actionTypes";

export const showNotification = (message, category) => ({
    type: SHOW_NOTIFICATION,
    payload: {
        message,
        category,
    },
});

export const closeNotification = () => ({
    type: CLOSE_NOTIFICATION,
    payload: null,
});

export const setSettings = (settings) => ({
    type: SET_SETTINGS,
    payload: settings,
});
